body {
  background: linear-gradient(95deg, #566360, #a8aea1) 0 0 / 400% 400%;
  animation: 21s infinite AnimationName;
}

@keyframes AnimationName {
  0% {
    background-position: 0 62%;
  }

  50% {
    background-position: 100% 39%;
  }

  100% {
    background-position: 0 62%;
  }
}

.section {
  background-color: #566360;
  width: 100%;
  height: 50px;
  margin-top: 8px;
  padding: 15px 0;
  position: fixed;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 100px 15px 15px;
}

.search-form {
  justify-content: center;
  display: flex;
}

.input {
  font-size: x-large;
}

.load-more.is-hidden {
  display: none;
}

.load-more {
  color: #000;
  background-color: gray;
  border-color: #000;
  border-radius: 8px;
  width: 150px;
  height: 50px;
  margin: 40px auto;
  font-size: medium;
  font-weight: bold;
  display: block;
}

.load-more:hover {
  cursor: pointer;
  background-color: #6495ed;
  border-color: #5f9ea0;
}

.button {
  background: #a9a9a9;
  font-size: medium;
}

.gallery {
  flex-wrap: wrap;
  flex-basis: calc(25% - 22.5px);
  gap: 30px;
  display: flex;
}

.card {
  width: 270px;
  height: 180px;
}

.card > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.photo-card {
  background-color: #f8f8ff;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 13px;
  display: flex;
}

